/* GalleryDetail.js */
.gallery-container {
  text-align: center;
  margin-top: 30px;
}

.swiper-container {
  margin-top: 20px;
}

.gallery-image {
  max-width: 80%;
  height: auto;
}

.back-link {
  margin-top: 30px;
}

/* GalleryList.js */
.gallery-list-container {
  text-align: center;
  margin-top: 50px;
}

.folder-links {
  display: flex;
  flex-direction: column; /* 요거 추가! */
  align-items: center; /* 가운데 정렬하려면 추가 */
  gap: 20px;
  margin-top: 30px;
}

.folder-link {
  font-size: 20px;
  text-decoration: none;
  color: inherit;
}

.back-link {
  margin-top: 30px;
}

a {
  color: inherit;
  text-decoration: none; 
}

a:hover {
  color: inherit;
  text-decoration: none;
}
