.container {
    text-align: center;
    margin-top: 50px;
    opacity: 0;
    transition: opacity 0.5s ease;
  }
  
  .container.visible {
    opacity: 1;
  }
  
  .slide-down {
    transform: translateY(-50px);
    opacity: 0;
    animation: slideDown 0.6s forwards;
  }
  
  @keyframes slideDown {
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* 약간의 시간 차를 주고 싶으면 */
  .slide-delay-1 {
    animation-delay: 0.2s;
  }
  .slide-delay-2 {
    animation-delay: 0.4s;
  }
  .slide-delay-3 {
    animation-delay: 0.6s;
  }
  .slide-delay-4 {
    animation-delay: 0.8s;
  }