.custom-link {
  background: none;
  border: none;
  color: inherit;
  text-decoration: none;
  font-size: 20px;
  cursor: pointer;
}

.loading-overlay {
  position: fixed; /* 화면 전체 덮기 */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white; /* 흰색 배경 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* 맨 위에 띄우기 */
}

.loading-spinner {
  width: 100px;
  height: 100px;
  animation: spin 0.5s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
